import React from 'react'
import { db, app } from '../../firebase'

import { collection, query, doc, updateDoc, setDoc, DocumentSnapshot, orderBy, limit, addDoc} from 'firebase/firestore'
import { useCollectionData, useDocumentData } from 'react-firebase-hooks/firestore'

import { RiArrowDownSFill, RiArrowUpSFill } from "react-icons/ri"

import "./bar.css"

export default function Bar(props) {
    const { displayName, percent, old } = props.stock;

    return (
      <div className='bar'>
            <h1>{displayName}</h1>
            <div>
                <h1>{percent.toFixed(2)}%</h1>{percent > old ? <RiArrowUpSFill size={60} color={'green'}/> : <RiArrowDownSFill size={60} color={'red'}/>}
            </div>
      </div>
    )
}