import React from 'react'
import { db, app } from '../../firebase'
import "./stock.css"
import { collection, query, doc, updateDoc, setDoc, DocumentSnapshot, orderBy, limit, addDoc} from 'firebase/firestore'
import { useCollectionData, useDocumentData } from 'react-firebase-hooks/firestore'

import { RiArrowDownSFill, RiArrowUpSFill } from "react-icons/ri"


export default function Stock(props) {
    const { displayName, percent, old } = props.stock;
    
    const bidsRef = collection(db, 'stocks', displayName, 'bids')
    const r = query(bidsRef)
    const [bids] = useCollectionData(r)

    const stockIncrease = () => {
        const docRef = doc(db, 'stocks', displayName)
        const theBid = {
            bid: 1
        }

        addDoc(bidsRef, theBid)

        var sum = 0;
  
        for (let i = 0; i < bids.length; i++) {
            if (bids.at(i).bid === 1){
                sum += 1;
            }
        }

        const data = {
            displayName: displayName,
            percent: sum/bids.length * 100,
            old: percent
        }
        setDoc(docRef, data)
    }

    const stockDecrease = () => {
        const docRef = doc(db, 'stocks', displayName)
        const theBid = {
            bid: -1
        }

        addDoc(bidsRef, theBid)

        var sum = 0;
  
        for (let i = 0; i < bids.length; i++) {
            if (bids.at(i).bid === 1){
                sum += 1 ;
            }
        }
        
        const data = {
            displayName: displayName,
            percent: sum/bids.length * 100,
            old: percent
        }
        setDoc(docRef, data)
    }


    return (
      <div className='stock'>
        <div className='stock_head'>
            <h1>{displayName}</h1>
            <div>
                <h1>{percent.toFixed(2)}%</h1>{percent > old ? <RiArrowUpSFill size={60} color={'green'}/> : <RiArrowDownSFill size={60} color={'red'}/>}
            </div>
        </div>
        <div className='stock_choice'>
            <button className='green' onClick={() => stockIncrease()} >Buy</button>
            <button className='red' onClick={() => stockDecrease()}>Sell</button>
        </div>
      </div>
    )
  }
